@import "src/assets/styles/mixins/fontsMixins.scss";


.headerWrapper {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.header {
  @include title;
  margin-left: 24px;
}

.select {
  cursor: pointer;
  width: 121.49px;
  height: 36px;
  background: $white;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  margin-right: 14px;
}

